import fetch from 'isomorphic-unfetch';
import { useState, useEffect } from 'react';
import Dashboard from '../components/Dashboard/Dashboard';

const Listings = ({listings})=>{
    const [listingToRender, changeListing] = useState([]);
    const [toggleStatus, toggle] = useState('userListings');
    const [testUser] = useState('abgfigu143668');
    useEffect(()=>{
        for (const listing in listings) {
            if(listing === testUser){
                console.log(listings[testUser].listings);
            };
        }
    });
    console.log(listings);
    
    return <Dashboard pageTitle="Listings - EazyRent" route="listings" toggle={toggle} toggleStatus={toggleStatus} listings={listings} user={testUser}/>;
}

Listings.getInitialProps = async()=>{
    const res = await fetch('http://localhost:3004/users');
    const data = await res.json();

    return {
        listings: data
    }
}

export default Listings;